body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#game-wrapper {
  margin: 40px auto;
  width: 500px;
  text-align: center;
  font-family: Consolas, Monaco, 'Andale Mono', monospace;
}

#game-board, #game-board * {
  box-sizing: border-box;
}

#game-board {
  display: inline-block;
  width: 304px;
  height: 304px;
  padding: 0;
  margin: 0;
  border: 2px solid black;
}

.tile,
.button {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-touch-callout: none;
}

.tile {
  width: 100px;
  height: 100px;
  float: left;
  line-height: 100px;
  font-size: 50px;
  /* background: #fff; */
  &:hover:not(:empty) {
      cursor: pointer;
      -webkit-transition: background 0.2s, -webkit-transform 0.2s;
      transition: background 0.2s, -webkit-transform 0.2s;
      transition: transform 0.2s, background 0.2s;
      transition: transform 0.2s, background 0.2s, -webkit-transform 0.2s;
      background: #eee;
  }
}

.win {
  -webkit-animation: winner 2s infinite;
          animation: winner 2s infinite;
}

.highlight,
.move-up,
.move-right,
.move-down,
.move-left {
  background: rgba(72, 172, 222, 0.75) !important;
}

.highlight {
  background: #fff;
  -webkit-animation: highlight 0.4s;
          animation: highlight 0.4s;
}

.move-up {
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
}
.move-right {
  -webkit-transform: translateX(100px);
          transform: translateX(100px);
}
.move-down {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}
.move-left {
  -webkit-transform: translateX(-100px);
          transform: translateX(-100px);
}

.button {
  display: inline-block;
  padding: 4px 10px;
  color: black;
  border: 2px solid black;
  &:hover { cursor: pointer; }
}

@-webkit-keyframes winner {
  0%   { background: rgba(72, 172, 222, 0.75); }
  50%  { background: rgba(72, 172, 222, 0.5); }
  100% { background: rgba(72, 172, 222, 0.75); }
}

@keyframes winner {
  0%   { background: rgba(72, 172, 222, 0.75); }
  50%  { background: rgba(72, 172, 222, 0.5); }
  100% { background: rgba(72, 172, 222, 0.75); }
}

@-webkit-keyframes highlight {
  0%   { background: rgba(72, 172, 222, 0.5); }
  100% { background: rgba(72, 172, 222, 0.8); }
}

@keyframes highlight {
  0%   { background: rgba(72, 172, 222, 0.5); }
  100% { background: rgba(72, 172, 222, 0.8); }
}
